.container{
    background-color: #47657a;
}
.wrapper{
    color:white;
    font-size: 25px;
    align-items: center;
    text-align: center;
    padding-top: 5%;
}
.Timer{
    font-size: 25px;
    align-items: center;
    text-align: center;
    padding-top: 5%;
    padding-bottom: 5%;
    color:white;
}
.button{
    background-color:rgb(153, 102, 25);
    color:white;
    display: inline-block;
    text-decoration: none;
    text-align: center;
    align-items: center;
    border:none;
    margin-left: 25%;
    margin-bottom: 5%;
    font-size: 20px;
    padding:25px 32px;
}
@media only screen and (max-width:600px){
    .container{
        width:100%;
       min-height: 100vh;
    }
}
@media only screen and (min-width:768px){
    .container{
        width:100%;
       min-height: 100vh;

    }
    .wrapper{
        font-size: 49px;
    }
    .Timer{
        font-size: 45px;
        margin-top: 25%;
        
    }
    .button{
        font-size:40px;
        margin-top:15%;
        padding: 55px 55px;

    }
}
@media only screen and (min-width:992px){
    .container{
        width:100%;
       min-height: 100vh;
    }
    .container ul{
        font-size:25px;
        display:grid;
        grid-template-columns: 1fr 1fr;
        margin-bottom:2%;
        padding-bottom: 2%;
        margin-top:-1%;
        align-items: center;
        text-align: center;
        width:50%;
        
    }
    .container ul li{
        list-style-type: none;
    }
    .wrapper{
        font-size:45px;
        
    }
    .Timer{
        margin-top:-4%;
        font-size:40px;
        font-weight: bolder;
        color:rgb(26, 23, 23);
        align-items: center;
        text-align: center;
    }
    .button{
        padding:22px 22px;
        font-size:35px;
        margin-top:-1%;
        margin-top: 2%;

    }
}
@media only screen and (min-width:1200px){
    .container{
        width:100%;
       min-height: 100vh;
    }
    .container ul{
        font-size:25px;
        display:grid;
