        text-align: center;
        width:50%;
        
    }
    .container ul li{
        list-style-type: none;
    }
    .wrapper{
        font-size:45px;
        
    }
    .Timer{
        margin-top:-4%;
        font-size:40px;
        font-weight: bolder;
        color:rgb(26, 23, 23);
        align-items: center;
        text-align: center;
    }
    .button{
        padding:22px 22px;
        font-size:35px;
        margin-top:-1%;
        margin-top: 2%;

    }
}
